body{
  background-color: rgb(0, 42, 97);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.container{
  display: flex;
	justify-content: space-between;
	align-items: center;
  background-color: white;
  position: relative;
  min-width: 200px;
	height: 100px;
	width: 70vw;
}
.container form{
  width: 100%;

  display: flex;
  align-items: center;
}
.container input{
  width: 80%;
  
  margin-left: 2%;
  justify-items: center;

}
.container button{
  background-color: rgb(252, 153, 24);
  border-radius: 25px;
  border: none;
  height: 3rem;
  width: 7rem;
}
label{
  color: white;
}